<template>
  <section class="questions">
    <el-form
      ref="question"
      :model="form"
      :rules="rules"
      style="width:80%;margin:0 auto"
    >
      <el-form-item
        v-for="(item,index) in questions"
        v-bind:key="index"
        :prop="item.name"
      >
        <el-row type="flex">
          <span class="question-item">{{index+1}}. {{item.question}}</span>
        </el-row>
        <el-row class="question-answer">
          <el-input
            v-if="item.type== 'input'"
            v-model="form[item.name]"
          ></el-input>
          <el-slider
            v-if="item.type == 'slide'"
            :min="item.start"
            :max="item.end"
            v-model="form[item.name]"
          ></el-slider>
          <el-row v-if="item.type=='multi-input'">
            <el-col
              :span="5"
              :offset="1"
              v-for="(inputItem,index) in item.children"
              class="multi-input"
              v-bind:key='index'
            >
              <el-input
                v-model="form[inputItem.name]"
                :type="number"
                :placeholder="inputItem.placeholder"
              ></el-input>
            </el-col>
          </el-row>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          @click="onSubmit('question')"
        >see recommendations</el-button>
        <el-button
          @click="onReset('question')"
          style="margin-left:30px"
        >reset</el-button>
      </el-form-item>
    </el-form>
    <el-row
      class="score-list"
      v-show="showList"
    >
      <el-col
        :offset="2"
        :span="16"
      >
        <h3>Core Modules</h3>
        <ul>
          <li
            v-for="(item,index) in coreList"
            v-bind:key="index"
          >
            {{item}}
          </li>
        </ul>
      </el-col>
      <el-col
        :offset="2"
        :span="16"
      >
        <h3>UE Modules</h3>
        <ul>
          <li
            v-for="(item,index) in ueList"
            v-bind:key="index"
          >
            {{item}}
          </li>
        </ul>
      </el-col>
      <el-col
        :offset="2"
        :span="16"
      >
        <h3>GE Modules</h3>
        <ul>
          <li
            v-for="(item,index) in geList"
            v-bind:key="index"
          >
            {{item}}
          </li>
        </ul>
      </el-col>
    </el-row>
  </section>
</template>
<script>
export default {
  name: "questions",
  data() {
    return {
      showList: false,
      form: {
        workload: 0,
        interest: 0,
        cap: 0,
        modules: "",
        core: "",
        core: "",
        ue: "",
        ge: ""
      },
      ueList: [],
      coreList: [],
      geList: [],
      rules: {
        core: [
          {
            required: true,
            message: "please enter the core",
            trigger: "blur"
          }
        ],
        ue: [
          {
            required: true,
            message: "please enter the ue",
            trigger: "blur"
          }
        ],
        ge: [
          {
            required: true,
            message: "please enter the ge",
            trigger: "blur"
          }
        ]
      },
      questions: [
        {
          name: "workload",
          question: "How much workload are you willing to put in?",
          type: "slide",
          start: 0,
          end: 10
        },
        {
          name: "interest",
          question: "How much do you prioritise your CAP this semester?",
          type: "slide",
          start: 0,
          end: 10
        },
        {
          name: "cap",
          question: "How much do you prioritise your CAP this semester?",
          type: "slide",
          start: 0,
          end: 10
        },
        {
          name: "core",
          question:
            "How many core/UE/GE modules respectively do you want to be allocated?",
          type: "multi-input",
          children: [
            {
              type: "number",
              name: "core",
              placeholder: "core"
            },
            {
              type: "number",
              name: "ue",
              placeholder: "ue"
            },
            {
              type: "number",
              name: "ge",
              placeholder: "ge"
            }
          ]
        }
      ]
    };
  },
  methods: {
    onSubmit(name) {
      var that = this;
      let url =
        "/api/student/module_codes/core_ue_ge?number1=" +
        that.form.core +
        "&number2=" +
        that.form.ue +
        "&number3=" +
        that.form.ge;
      this.$refs[name].validate(valid => {
        if (valid) {
          that.$http
            .get(url, {
              "Content-Type": "application/json;charset=UTF-8"
            })
            .then(function(data) {
              that.showList = true;
              console.log(data);
              that.ueList = data.data.ue.modules || [];
              that.geList = data.data.ge.modules || [];
              that.coreList = data.data.core.modules || [];
            })
            .catch(function() {
              console.log("xxx");
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    onReset(name) {
      this.$refs[name].resetFields();
    }
  }
};
</script>
<style>
.questions {
  padding: 20px;
}
.question-item {
  font-size: 20px;
}
.question-answer {
  padding-left: 20px;
}
.score-list {
  text-align: left;
}
</style>




